.formControl { 

    position: relative;

    &.error input,  &.error textarea {
        border: solid red 0.2vh; 
    }

    & span {
        display: block;
        position: absolute;
        z-index: 3;
        top: 40%;
        left: 33%;
        background: linear-gradient(45deg, rgb(8, 8, 72) 80%, rgb(11, 11, 132) 90%, rgb(10, 10, 171) 95%);

        visibility: hidden;
    }

    &.error span { 
        visibility: visible; 
    }


    &.warning input,  &.warning textarea {
        border: solid rgb(121, 121, 0) 0.2vh;
    }
   
} 

 


