
a:link { color:white; font-weight: 700; font-style: italic; text-decoration: none;}
a:visited { color:rgb(60, 193, 115) }
a:hover { color:rgb(115, 255, 0) }
a:active { color:rgb(255, 0, 98) }

::-webkit-scrollbar {
  width: 1vh; 
}
  
/* Track */
::-webkit-scrollbar-track { 
background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
background: rgb(255, 255, 255); 
border-radius: 0 1vh 1vh 0; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
background: red; 
}




