 
.whole-app-page-wrapper {
  // background-color: rgb(57, 56, 65);
  width: 100vw;
  min-height: 100vh;
  max-height: max-content;

  font-size: 2vh;
  color: rgb(7, 229, 118);
}

.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 80vw;
  min-height: 100vh; 
  grid-template-areas:
  "h h"
  "n c";

  grid-template-rows: 10vh 1fr;
  grid-template-columns: 2fr 10fr; 
}

/*просто занимает место пока не выбраны подполя контента в юрле*/
.app-wrapper-content {
  grid-area: c;
  position: relative;
}

.content-background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url('https://eskipaper.com/images/blue-space-3.jpg') no-repeat fixed;
  background-size: cover; 
  opacity: 0.9;
  z-index: -1;
}

textarea, input {resize:none;}