

%thlChatBorder {
    border-radius: 2vw 2vw 2vw 2vw;
    border: solid rgb(7, 176, 238) 0.3vh;
}
 
.currentChat { 
    // max-height: 90vh;
    
    background: url('https://i0.wp.com/www.techgrapple.com/wp-content/uploads/2016/07/WhatsApp-Chat-theme-iPhone-3.jpg?fit=744%2C1392&ssl=1') repeat fixed;
    background-size: fill; 

    font-family: "Lucida Console";
    display: grid;
    grid-template-areas:
    "chatsHeader"
    "messages" 
    "createMessageForm";

    grid-template-rows: 10vh 55vh 25vh; 
    margin: 0; 

    .chatsHeader { 
        grid-area: chatsHeader;
        display: grid;
        grid-template-areas:
        "chatsHeaderPhoto chatsHeaderName chatsHeaderSettings";
        grid-template-columns: 20vh 2fr 1.2fr;
        grid-template-rows: 100%; 
         
    }

    // header photo
    .chatsHeaderPhoto {
        align-self: center;
        grid-area: chatsHeaderPhoto;
        height: 100%;

        .chatsHeaderPhotoAlt { 

            text-align: center;
            padding-top: 3vh; 
        }

        & > img {
            object-fit: cover;
            height: 100%;
            width: 100%;
            @extend %thlChatBorder;
            padding: 0;
            margin: 0;
        
        }
    }
     
    // header  name
    .chatsHeaderName {
        grid-area: chatsHeaderName; 
        font-weight: 600;
        font-family: Courier;
        padding-left: 5vh; 
        width: inherit;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;  
        border: inherit;
        border-radius: inherit; 
        padding-top: 1vh; 
    }
    
    // header setttings
    .chatsHeaderSettings {
        padding-left: 5vh;
        grid-area: chatsHeaderSettings;
        position: relative;
        border: inherit;
        border-radius: inherit;
    
        & .chatsSettings {
            display: none;
        }
    
        &:hover .chatsSettings {
            display: block;
            position: absolute;
            grid-template-areas:
            "chatsSettingsItem";
            grid-template-rows: repeat(auto-fit, 1fr);
            z-index: 9999;


            .chatsSettingsItem {   
                
                .chatsSettingsItemForm {
                    display: none;
                }
            
                position: relative;
                display: grid;
            
                &:hover{
                    .chatsSettingsItemForm{
                        display: block;
                        position: absolute;
                        left: 16vh;
                        z-index: inherit;
                        background-color: white;
                    } 
                    .chatsSettingsItemH { 
                        width:200%; 
                    }
                } 
            }


        } 
    }

 
    // messages

    .messages { 
        display: grid;
        margin-top: 2vh;
        grid-area: messages;
        grid-template-areas:
        "message";
        grid-template-rows: repeat(auto-fill, minmax(10vh, 1fr)); 
        overflow-y: scroll; 
    }


    .message {
        position: relative; 
        display: grid; 
        grid-template-areas:
        "messageAuthorName messageBody messageSendedEdited"
        ". messageFile deleteMessage";
        grid-template-columns: 10vw 1fr 15vw;
        grid-template-rows: 1fr 8vh; 
        padding-left: 2vh; 
        @extend %thlChatBorder;
    } 
    
    .messageAuthorName{
        display: grid;
        grid-area: messageAuthorName;
    
        & > a {
            width: inherit;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden; 
        }
    } 

    .messageBody { 
        grid-area: messageBody; 
        font-weight: 400; 
        font-style: italic;
        padding: 0 1vw 0 1vw;
        text-align: left;
        max-width: 90vh; 
        word-break: break-all; 
    } 

    .messageSendedEdited { 
        grid-area: messageSendedEdited; 
        grid-template-areas:
        "messageSended"
        "messageEdited";
        grid-template-rows: 50% 50%; 
    } 
    .messageSended {
        grid-area: messageSended; 
    }
    .messageEdited {
        grid-area: messageEdited; 
    }

    .messageFile {
        grid-area:messageFile;
        position: absolute;
    }
    
    .deleteMessage{
        grid-area: deleteMessage; 
        font-size: 2.5vh; 
        & a {
            display: block;
            position: relative;
            top: 2vh;
            text-align: center; 
        } 
    } 

    // Form

    .createMessageForm { 
        grid-area: createMessageForm;  
        position: relative; 
        display: grid;
        grid-template-areas: 
        "createMessageFormH"
        "redux_form";
        grid-template-rows:  1fr, 3fr; 
        // grid-template-columns: 1fr 1fr; 

        input{
            height: 5vh;
            width: fit-content;
        }
        textarea {
            height: 10vh;
            width: 90%;
        } 
    }
    
    .createMessageFormH {
        grid-area: createMessageFormH; 
        text-align: center;
        color: rgb(106, 248, 215);
        align-self: center;
    }

}

 

// edit message 
.editMessage {
    position: relative;
    display: grid;
    grid-area: messageBody; 
    height: 500%;
    grid-template-areas: 
    "editField editField"
    ". countChars"
    "errorField errorField";
    grid-template-rows: 1fr 2vh;
    grid-template-columns: 1fr 5vh;
}  
.editField{
    grid-area: editField; 
} 
.errorField {
    position: absolute;
    grid-area: errorField; 
    top: -5vh;
    left: 1vh;
} 
.countChars {
    position: absolute;
    grid-area: countChars;
    text-align: left; 
    left: -7vh;
    top: -3vh; 
}
