.fileImageActivate {
    height: 6vh;
    width: 8vh; 
    & > img {
        display: block;
        object-fit: cover;
        height: 100%;
        width: 100%;
        padding: 0;
        margin: 0;
    }
    & > span, & > button {
        display: none;
    }
} 
.fileImageDeactivate{
    & > img{
        display: none;
    }
    & > span, & > button{
        display: block;
    } 
} 


.usersForChatActive{
    display: block;
    position: absolute; 
    left: 40vh;
    width: 30vh;
    height: 30vh; 
    overflow-y: scroll; 
    grid-template-areas:
    "userForChat";
    grid-template-rows: repeat(auto-fit, 2vh);
} 
.userForChat {
    grid-area: userForChat;
    height: 10vh;
    width: 10vh; 
} 



